.main {
  position: relative;
  height: calc(100% - 90px);
  min-height: 100%;
  margin: 0 auto;
  padding: 30px 0 30px;
  width: 100%;
  max-width: 81.25vw;

  @media screen and (max-width: 768px) {
    max-width: 90vw;
  }

  @media screen and (max-width: 425px) {
    padding: 30px 0 50px;
  }
}